<!-- 统一门户首页主体区域 -->
<template>
	<div class="container">
		<div class="carousel-wrap">
			<img src="../../assets/image/banner-test-1.jpg" alt="">
		</div>
		<div class="content">
			<!--tips-->
			<div class="tips-wrap">
				<el-row :gutter="20">
					<el-col v-for="item in tips" :key="item.icon" :span="6">
						<div class="tip-item">
							<i :style="{color:item.color}" :class="['iconfont',item.icon]"></i>
							<div>
								<p>{{item.number}}</p>
								<span>{{item.title}}</span>
							</div>
						</div>
					</el-col>
				</el-row>
			</div>
			<!--news-->
			<div class="news-wrap">
				<el-row :gutter="20">
					<el-col :span="16">
						<div class="news">
							<div class="title">新闻</div>
							<div class="news-content">
								<div class="news-cover"></div>
								<ul class="news-list">
									<li @click="handleToNews" v-for="(item,index) in doorData.newInfos" :key="index" :id="item.id">
										<span class="text-overflow">{{item.name}}</span>
										<span>{{item.createTime}}</span>
									</li>
								</ul>
							</div>
						</div>
					</el-col>
					<el-col :span="8">
						<div class="notice">
							<div class="title">通知</div>
							<ul class="notice-list">
								<li @click="handleToNews" class="text-overflow" v-for="(item,index) in doorData.notice" :key="index" :id="item.id">
									{{item.name}}
								</li>
							</ul>
						</div>
					</el-col>
					<el-col></el-col>
				</el-row>
			</div>

			<!--资源平台-->
			<div class="platform-wrap">
				<div class="title">
					<router-link tag="span" to="/resource/Layout/index">资源平台</router-link>
				</div>
				<el-row :gutter="20" type='flex'>
					<el-col :span="10">
						<div class="plat-charts">
							<div class="item">
								<div class="left">
									<p>{{doorData.resourceNumber}}</p>
									<p>资源总数</p>
								</div>
								<div class="right">
									<img src="./../../assets/image/curve_home_pic_.png" alt="">
								</div>
							</div>
							<div class="item">
								<div class="left">
									<p>+{{doorData.newResInWeek}}</p>
									<p>本周新增</p>
								</div>
								<div class="right">
									<img src="./../../assets/image/curve_home_pic_.png" alt="">
								</div>
							</div>
							<div style="width:50%;height:300px" id="platform1-wrap"></div>
							<div style="width:50%;" id="platform2-wrap"></div>
						</div>
					</el-col>
					<el-col :span="14">
						<div class="course-boxs">
							<res-box v-for="item in resourceList" :info="item" :key="item.id"></res-box>
						</div>
					</el-col>
				</el-row>
			</div>

			<!--同步课堂-->
			<div class="sync-wrap">
				<div class="title">
					<router-link tag="span" to="/sync/Layout/index">同步课堂</router-link>
				</div>
				<div class="sync-content">
					<el-row :gutter="20" type="flex">
						<el-col :span="12">
							<div class="map-cover">
							</div>
						</el-col>
						<el-col :span="12">
							<div class="area-info">
								<div class="name">{{areaInfo.areaName}}</div>
								<div class="info-wrap">
									<div>
										<p>{{areaInfo.totalNumber}}</p>
										<span>总计开课</span>
									</div>
									<div>
										<p>{{areaInfo.goingNumber}}</p>
										<span>正在进行</span>
									</div>
									<div>
										<p>{{areaInfo.schoolNumber}}</p>
										<span>参与学校</span>
									</div>
									<div>
										<p>{{areaInfo.teacherNumber}}</p>
										<span>参与教室</span>
									</div>
								</div>
								<div class="rank">
									<div class="teacher-rank">
										<div class="title">教师排名</div>
										<ul class="rank-list">
											<li v-for="(item,i) in areaInfo.schoolTops" :key="i">
												<span>{{i}}</span>
												<span class="text-overflow">{{item.name}}</span>
												<span>{{item.number}}节</span>
											</li>
										</ul>
									</div>
									<div class="school-rank">
										<div class="title">学校排名</div>
										<ul class="rank-list">
											<li v-for="(item,i) in areaInfo.teacherTopList" :key="i">
												<span>{{i}}</span>
			                  <span class="text-overflow">{{item.name}}</span>
												<span>{{item.number}}节</span>
											</li>
										</ul>
									</div>
								</div>
							</div>
						</el-col>
					</el-row>
				</div>
			</div>

			<!--预约中心-->
			<div class="order-wrap">
				<div class="title">
					<router-link tag="span" to="/order/Layout/index">预约中心</router-link>
				</div>
				<div class="order-content">
					<el-row :gutter="20" type="flex">
						<el-col :span="4">
							<div class="order-left">
								<div class="top">
									<p>582</p>
									<p>累计预约</p>
								</div>
								<img src="./../../assets/image/histogram.png" alt="">
							</div>
						</el-col>
						<el-col :span="20">
							<div style="height:350px" id="order-charts"></div>
						</el-col>
					</el-row>
				</div>
			</div>
			<!--直播中心-->
			<div class="live-wrap">
				<div class="title">
					<router-link tag="span" to="/liveCenter/Layout/index">直播中心</router-link>
				</div>
				<div class="live-content">
					<el-row :gutter="20" type="flex">
						<el-col :span="6">
							<div class="live-left">
								<div class="item">
									<div class="left">
										<p>{{doorData.livingNumber}}</p>
										<p>正在直播</p>
									</div>
									<div class="right">
										<img src="./../../assets/image/curve_home_pic_.png" alt="">
									</div>
								</div>
								<div class="item">
									<div class="left">
										<p>+{{doorData.lookerNumber}}</p>
										<p>观看人数</p>
									</div>
									<div class="right">
										<img src="./../../assets/image/curve_home_pic_.png" alt="">
									</div>
								</div>
							</div>
						</el-col>
						<el-col :span="18">
							<div class="live-right">
								<div class="type-control">
									<div @click="currentActive='living'" :class="{active:currentActive==='living'}">正在直播</div>
									<div @click="currentActive='liveBroad'" :class="{active:currentActive==='liveBroad'}">直播报告</div>
								</div>
								<div class="live-box">
									<template v-if="currentActive==='living'">
										<live-box :info="item" v-for="item in doorData.livingList" :key="item.id"></live-box>
									</template>
									<template v-else>
										<liveBroad :info="item" v-for="item in doorData.appLiveList" :key="item.id"></liveBroad>
									</template>
								</div>
							</div>
						</el-col>
					</el-row>
				</div>
			</div>
		</div>
		<res-footer></res-footer>
	</div>
</template>

<script>
	import echarts from 'echarts'
	export default {
		components: {
			liveBroad: () => import("@/components/live-broad"),
			resBox: () => import("@/components/res-box"),
      resFooter: () => import("@/components/res-footer"),
      liveBox:() => import("@/components/live-box")
		},
		created() {
      this.getSynClassObj()
			this.getIndexData()
		},
		mounted() {
			this.initOrderCharts();
			this.initPlatformCharts1();
			this.initPlatformCharts2();
		},
		data() {
			return {
				doorData:{},
				currentActive: 'living', //正在直播 / 直播报告
				orderOption: {
					tooltip: {
						trigger: 'axis'
					},
					legend: {
						data: ['邮件营销', '联盟广告', '视频广告']
					},
					xAxis: {
						type: 'category',
						data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
					},
					yAxis: {
						type: 'value'
					},
					series: [{
							name: '邮件营销',
							data: [820, 932, 901, 934, 1290, 1330, 1320],
							type: 'line',
							smooth: true
						},
						{
							name: '联盟广告',
							data: [1220, 932, 901, 2934, 190, 1330, 130],
							type: 'line',
							smooth: true
						},
						{
							name: '视频广告',
							data: [820, 2932, 901, 934, 1290, 130, 1320],
							type: 'line',
							smooth: true
						}
					]
				},
				platformOption: {
					series: [{
						name: '访问来源',
						type: 'pie',
						radius: ['50%', '70%'],
						avoidLabelOverlap: false,
						label: {
							normal: {
								show: false,
								position: 'center'
							},
							emphasis: {
								show: true,
								textStyle: {
									fontSize: '30',
									fontWeight: 'bold'
								}
							}
						},
						labelLine: {
							normal: {
								show: false
							}
						},
						data: [{
								value: 335,
								name: '直接访问'
							},
							{
								value: 310,
								name: '邮件营销'
							},
							{
								value: 234,
								name: '联盟广告'
							},
							{
								value: 135,
								name: '视频广告'
							},
							{
								value: 1548,
								name: '搜索引擎'
							}
						]
					}]
				},
				tips: [{
						icon: 'icon-apartment',
						title: '接入系统',
						number: '6',
						color: '#684EE6'
					},
					{
						icon: 'icon-school',
						title: '学校总数',
						number: '123',
						color: '#0A83FF'
					},
					{
						icon: 'icon-video',
						title: '录播教室',
						number: '200',
						color: '#FA821D'
					},
					{
						icon: 'icon-user',
						title: '注册教师',
						number: '420',
						color: '#30B89D'
					},
        ],
        resourceList:[],
        areaInfo:{}
			}
		},
		methods: {
			getIndexData() {
				this.$api.getTYIndexData().then(res => {
					this.doorData = res.data
					this.tips[0].number = res.data.systemNumber
					this.tips[1].number = res.data.schoolNumber
					this.tips[2].number = res.data.recordRoomNumber
          this.tips[3].number = res.data.teacherNumber
          /* 资源名称 */
          res.data.resources.forEach(item => {
            this.resourceList.push({
              resourceFileTime:item.burningTime,
              resourceName:item.name,
              userName:item.userName,
              avgScore:item.score,
              id:item.id,
              visitCount: item.lookTimes,
            })
          })
          /* 直播 */
				})
      },
      getSynClassObj(){
        const params = {id:'28587'};
        this.$api.getSynClassObj(params).then(res => {
          // console.log(res);
          this.areaInfo = res.data;
        })
      },
			/* ======= */
			initOrderCharts() {
				let myChart = echarts.init(document.getElementById('order-charts'));
				myChart.setOption(this.orderOption);
			},
			initPlatformCharts1() {
				let myChart = echarts.init(document.getElementById('platform1-wrap'));
				myChart.setOption(this.platformOption);
			},
			initPlatformCharts2() {
				let myChart = echarts.init(document.getElementById('platform2-wrap'));
				myChart.setOption(this.platformOption);
			},
			handleToNews() {
				const link = this.$router.resolve({
					path: '/Layout/index/notice-detail'
				});
				window.open(link.href, '_blank');
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
