		height: 360px;

		img {
			width: 100%;
			height: 100%;
		}
	}

	.content {
		width: 80%;
		margin: 0 auto;
	}

	.tips-wrap {
		padding: 30px 0 18px;
	}

	.tips-wrap .tip-item {
		height: 128px;
		background: #F5F7FB;
