		display: flex;
		align-items: center;
		padding-left: 48px;
	}

	.tips-wrap .tip-item i {
		font-size: 60px;
		margin-right: 5px
	}

	.tips-wrap .tip-item div p {
		color: #333;
		font-size: 28px
	}

