			}

			li:hover {
				span {
					color: rgba(64, 159, 255, 1);
				}
			}

			li span {
				color: #666;
			}

			li span:first-child {
				display: inline-block;
				max-width: 80%;
			}

			li span:last-child {
				flex-shrink: 0
			}
		}
	}

	.news-wrap .notice {
		padding-left: 20px;
