		>li {
			margin-bottom: 10px;
			cursor: pointer;
		}

		>li:hover {
			color: #2695FF;
		}
	}

	.platform-wrap .title {
		border-bottom: 1px solid #E5E5E5;
		line-height: 48px;

		span {
			cursor: pointer;
