	}

	.sync-wrap .sync-content .area-info {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		margin-left: 30px;

		.name {
			font-size: 36px;
			color: #333;
		}

		.info-wrap {
			margin: 10px 0;
			display: flex;
			justify-content: space-between;
			text-align: center;

			p {
				color: #333333;
				font-size: 30px;
			}

			span {
				color: #909399;
			}
		}

		.rank {
			display: flex;
			justify-content: space-between
		}

		.rank .teacher-rank,
		.school-rank {
