				}

				li span:nth-child(1) {
					color: #909399
				}

				li span:nth-child(2) {
					// margin: 0 80px 0 30px
          display: inline-block;
          margin-left:20px; 
          min-width: 200px;
				}

				li span:nth-child(3) {
					color: #909399
				}

				li:nth-child(1) {
					background: url('./../../assets/image/gold_medal_home_pic.png') no-repeat;

					span:first-child {
						color: transparent
					}
				}

				li:nth-child(2) {
					background: url('./../../assets/image/silver_medal_home_pic.png') no-repeat;

					span:first-child {
						color: transparent
					}
				}

				li:nth-child(3) {
					background: url('./../../assets/image/bronze_medal_home_pic.png') no-repeat;

					span:first-child {
						color: transparent
					}
				}
			}
		}
	}

	.order-wrap {
		margin-top: 30px;
	}

	.order-wrap>.title {
		border-bottom: 1px solid #E5E5E5;
