		}

		.top p:last-child {
			color: #909399;
		}
	}

	.live-wrap {
		margin-top: 30px;
	}

	.live-wrap>.title {
		border-bottom: 1px solid #E5E5E5;
		line-height: 48px;

		span {
			color: #333;
